import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import { Grid, Typography, Button, TextField, Link, FormControlLabel, Checkbox } from "@mui/material";
import * as yup from "yup";
import { makeStyles } from "@mui/styles";
import ArrowForwardRoundedIcon from "@mui/icons-material/ArrowForwardRounded";
import { useFormik } from "formik";
import { ReactComponent as GoogleIcon } from "assets/GoogleIcon.svg";
import { useNavigate } from "react-router";
import clsx from "clsx";
import { AI_signup, AI_user_detail } from "API/userAPI";
import {
  useAIEventHandler,
  useInstance
} from "easy-redux-http-call"
import { generateFormikInput, saveJWTToken } from "utils";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { DateTimePicker, LocalizationProvider } from "@mui/x-date-pickers";
import { useEffect, useMemo } from "react";

const useStyles = makeStyles((theme) => ({
  dmSignupRoot: {
    padding: 15,
    marginTop: 60,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  dmSignupContentContainer: {
    width: "100%",
    "&>h4": {
      fontWeight: 600,
    },
    "&>p": {
      color: theme.palette.grey[500],
      marginTop: 5,
    },
  },
  dmLoginForm: {
    marginTop: 10,
    "&>div": {
      marginTop: 10,
    },
    "&>button": {
      marginTop: 10,
      justifyContent: "space-between",
    },
  },
  dmOrPartition: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    margin: "10px 0px",
    "&:before, &:after": {
      content: '""',
      display: "block",
      width: "100%",
      height: 1,
      backgroundColor: theme.palette.grey[300],
      margin: "0px 3px",
    },
  },
  dmSignUpWithGoogleButton: {
    justifyContent: "flex-start !important",
    background: `${theme.palette.grey[100]} !important`,
    "&>svg": {
      marginRight: 15,
    },
  },
  dmLoginAgain: {
    marginTop: "3px !important",
    textAlign: "right",
  },
  dmDateInputContainer: {
    "&>div": {
      width: "100%"
    }
  }
}));

const passwordRules = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{5,}$/;
const signupSchema = yup.object().shape({
  name: yup.string().required("Required"),
  email: yup.string().email("Invalid email").required("Required"),
  password: yup
    .string("Required")
    .required("Password is required")
    .matches(passwordRules, { message: "Please create a stronger password" }),
  birthdate: yup.date(),
  containYear: yup.boolean(),
  containTime: yup.boolean(),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref("password"), null], "Passwords must match")
    .required("Password is required"),
});

function Signup() {
  const classes = useStyles();
  const navigate = useNavigate();

  const handleSignup = (values, actions) => {
    AI_signup.call({
      params: values
    })
  };

  const signupState = useInstance(AI_signup)

  useAIEventHandler(
    AI_signup,
    {
      onSuccess: (res) => {
        let token = res?.data?.token
        if (token) {
          saveJWTToken(res.data.token)
          AI_user_detail.call()
          navigate("/home")
        }
      },
      onError: (res) => {
        setErrors({
          password: res.error.error
        })
      }
    })

  const formik = useFormik({
    initialValues:
    {
      name: "",
      email: "",
      password: "",
      birthdate: new Date(),
      containYear: true,
      containTime: true,
      confirmPassword: ""
    },
    validationSchema: signupSchema,
    onSubmit: handleSignup,
  });
  const {
    values,
    handleChange,
    handleBlur,
    errors,
    touched,
    setErrors,
    handleSubmit,
    setFieldValue
  } = formik

  const generateFormikProps = (key) => {
    return {
      fullWidth: true,
      name: key,
      value: values[key],
      onChange: handleChange,
      onBlur: handleBlur,
      helperText: touched[key] && errors[key],
      error: touched[key] && errors[key],
    };
  };

  const birthdateFormat = useMemo(() => {
    let format = "dd/MM"
    if (values.containYear) {
      format += "/yyyy"
    }
    if (values.containTime) {
      format += " hh:mm a"
    }

    return format

  }, [values.containYear, values.containTime])

  return (
    <HeaderWrapper>
      <Grid className={classes.dmSignupRoot}>
        <Grid className={classes.dmSignupContentContainer}>
          <Typography variant="h4"> Signup </Typography>
          <Typography variant="body2"> Please Sign up to continue </Typography>
          <form className={classes.dmLoginForm} onSubmit={handleSubmit}>
            <TextField
              {...generateFormikProps("name")}
              placeholder="Name"
              label="Name"
            />
            <TextField
              {...generateFormikProps("email")}
              placeholder="Email"
              label="Email"
            />
            <Grid className={classes.dmDateInputContainer}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DateTimePicker label="Event time"
                  format={
                    birthdateFormat
                  }
                  inputFormat="dd/MM/yyyy"
                  renderInput={(params) => <TextField {...params} />}
                  {...generateFormikInput(formik, "birthdate")}
                  onChange={(e) => {
                    setFieldValue("birthdate", e)
                  }}
                />
              </LocalizationProvider>
              <FormControlLabel label="Include year"
                control={<Checkbox
                  checked={values.containYear}
                  onChange={() => setFieldValue("containYear", !values.containYear)}
                />}
              />
              <FormControlLabel label="Include year"
                control={<Checkbox
                  checked={values.containTime}
                  onChange={() => setFieldValue("containTime", !values.containTime)}
                />}
              />
            </Grid>

            <TextField
              {...generateFormikProps("password")}
              placeholder="Password"
              label="Password"
            />
            <TextField
              {...generateFormikProps("confirmPassword")}
              placeholder="Confirm Password"
              label="Confirm Password"
            />
            <Button
              fullWidth
              endIcon={<ArrowForwardRoundedIcon />}
              type="submit"
              size="large"
              disabled={signupState.loading}
            >
              Sign Up
            </Button>
          </form>
          <Typography variant="subtitle2" className={classes.dmLoginAgain}>
            Already have account?&nbsp;
            <Link underline="hover" variant="subtitle2" href="/login">
              Login
            </Link>
          </Typography>
          <Grid className={classes.dmOrPartition}>
            <Typography> OR </Typography>
          </Grid>
          <Button
            fullWidth
            variant="contained"
            className={
              clsx(classes.dmSignUpWithGoogleButton,
                "comming-soon"
              )
            }
            size="large"
          >
            <GoogleIcon width="35" height="35" />
            Continue with Google
          </Button>
        </Grid>
      </Grid>
    </HeaderWrapper>
  );
}

export default Signup;
